.pAbout .w-100 h1.playFair {
  margin: 0 auto;
  width: 125rem;
  margin-bottom: 3rem;
}

.pAbout .w-100 p.officeP {
  margin: 0 auto;
}

.actualTab h2 {
  color: #072448;
  font-size: 4rem;
  font-family: Playfair Display;
  margin: 1.5rem 0;
}
.actualTab h6 {
  font-size: 2.1rem;
  color: #fff;
  font-family: Playfair Display;
}

.actualTab .num {
  color: #072448;
  font-size: 2.2rem;
}

.actualTab {
  border-bottom: 2px solid rgba(255, 255, 255, 0.5);
  padding: 3rem 2rem;
  border-radius: 2rem 0 0 2rem;
}

.tableContain {
  transform: translateX(15%);
  margin: 2rem 0;
}

.cusTable h1.playFair {
  margin-left: 15rem;
  margin-bottom: 1.5rem;
}

.actualTab2 {
  display: flex;
  align-items: center;
  border-bottom: 2px solid #bae8e8;
  padding: 4rem 0;
}

