.openPosition .position {
  border: 2px solid #bae8e8;
  padding: 2rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 80%;
  margin: 2rem auto;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.1) ,  -3px -3px 5px rgba(0, 0, 0, 0.1);
  border-radius: 0.5rem;
  transition: all 0.3s linear;
}

.openPosition .position:hover {
  transform: scale(1.05);
}

.openPosition .position h2 , .openPosition .position  h3 , .openPosition .position h5 {
  color: #e3f6f5;
}

.openPosition .position h5 {
  color: #413A60;
  font-weight: 600;
  font-size: 1.4rem;
}

.openPosition {
  cursor: pointer;
  min-height: 70vh;
}

.openPosition .position .posDiv button {
  background: transparent;
  border: 0;
  outline: 0;
  color: #413A60;
  padding: 0.5rem;
  text-transform: uppercase ;
  font-size: 1.5rem;
  font-weight: 600;
  position: relative;
}

.openPosition .position .posDiv {
  flex: 1 1 ;
  max-width: 20%;
  /* padding-left: 3rem; */
}



.openPosition .position .posDiv button::before {
  content: '';
  position: absolute;
  border-bottom: 2px solid #413A60;
  width: 5%;
  height: 0%;
  right: 5px;
  bottom: 2px;
  transition: all 0.4s ease-out;
}

.openPosition .position:hover .posDiv button::before {
  width: 90%;
}


