.serviceSvg svg {
  width: 100%;
  height: auto;
  pointer-events: none;
}

.serviceSvg svg #message ,.serviceSvg svg #calender ,.serviceSvg svg #clock  ,.serviceSvg svg #setting   {
  transform-origin: right;
  animation: spins 1s linear  infinite ;
  animation-direction: alternate;
}

.serviceSvg svg #calender{
  animation-delay: 0.2s;
}
.serviceSvg svg #clock{
  animation-delay: 0.4s;
}
.serviceSvg svg #setting{
  animation-delay: 0.6s;
}

.serviceSvg svg #setting{
  animation-delay: 0.6s;
}

.servicePage .actualTab {
  display: flex;
