.roadMap {
  width: 100%;
}
.road {
  margin: 2rem auto;
  max-width: 1200px;
}

.actualRoad {
  display: flex;
  flex-direction: column;
  max-width: 450px;
  position: relative;
  box-shadow: 3px 3px 1px rgba(255, 255, 255, 0.2) ,  -3px -3px 1px rgba(255, 255, 255, 0.2);
  border-radius: 1rem;
padding: 1rem;
}

.actualRoad:nth-child(even){
  text-align: left;
  margin-left: auto;
}

.actualRoad:nth-child(odd){
  text-align: right;
}

.actualRoad h3 {
  font-size: 3rem;
  color: #413A60 ;
  font-weight: 600;
  margin: 1rem 0 ;
}
.actualRoad p {
  font-size: 1.5rem;
  margin: 0;
}
.actualRoad p.num {
  font-size: 2.5rem;
  font-weight: 600;
}

.actualRoad:nth-child(even)::after , .actualRoad:nth-child(odd)::before {
  content: "";
  position: absolute;
  width: 2.5rem;
  height: 2.5rem;
  background: #fff;
  border-radius: 50%;
  box-shadow: 0 0 17px #fff;
  right: -15%;
  top: 50%;
}

.actualRoad:nth-child(even)::after{
  right: unset;
  left: -15%;
}

/* Animation  */


.Careercircle {
  background-color: #ffa822;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  z-index: 100;
  pointer-events: none;
  animation: fade 1s linear, disapp 0.1s linear 0.4s;
  animation-fill-mode: forwards;
  
}
.Aboutcircle {
  background-color: #f55951;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  z-index: 100;
  pointer-events: none;
  animation: fade 1s linear, disapp 0.1s linear 0.4s;
  animation-fill-mode: forwards;
}
.contactCircle {
  background-color: #00E472;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  z-index: 10;
  pointer-events: none;
  animation: fade 1s linear, disapp 0.1s linear 0.4s;
  animation-fill-mode: forwards;
}



.serviceCircle {
  background-color: #998bff;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  z-index: 10;
  pointer-events: none;
  animation: fade 1s linear, disapp 0.1s linear 0.4s;
  animation-fill-mode: forwards;
}

@keyframes fade {
  0%{
    opacity: 1;
    transform: scale(1);
  }
  100%{
    transform: scale(60);
    /* opacity: 0; */
  }
}

@keyframes disapp {
  0%{
    height: 10rem;
  }
  100%{
    height: 0;
    visibility: hidden;
  }
}

